function _initPassword() {
    // if keybaord security is applied, reset inner password
    if (webnpki_config.keyboardSecurity === 'default') {
        webnpki.setPassword();
    }

    $("#qlalfqjsgh, ask-qlalfqjsgh").val("");
}

function _getParameterMap(message) {
    var parameters = new Object();
    var pattern = new RegExp("(\\w+)=([^}]*)", "mg");
    var matchArray;
    while ((matchArray = pattern.exec(message)) != null) {
        parameters[matchArray[1]] = matchArray[2];
    }

    return parameters;
}

function _isNumber() {
    if (event.keyCode < 48 || event.keyCode > 57) {
        event.returnValue = false;
    }
}

export default KICA_DIALOG;